th {
  text-align: left;
  white-space: nowrap;
}
td {
  white-space: nowrap;
}

.left-cell {
  padding-right: 1em;
  text-align: right;
}

.right-cell {
  padding-left: 1em;
}

.icon {
  text-decoration: none
}

.ralign {
  text-align: right;
}

table {
  border-collapse: collapse;
}

tfoot {
  border-top: 1px solid #CCC;
}

tbody td:last-child {
  padding-bottom: 0.2em;
}

tbody th:last-child {
  padding-bottom: 0.2em;
}

tfoot td {
  padding-top: 0.2em;
}

.disconnected {
  background-color: #F5D76E;
}

/* Not a fan... */
th.rotate {
  height: 180px; /* ugh */
  white-space: nowrap;
}

th.rotate > div {
  -webkit-transform:
    translate(10px, 155px) /* ugh */
    rotate(315deg);
      -ms-transform:
    translate(10px, 155px) /* ugh */
    rotate(315deg);
          transform:
    translate(10px, 155px) /* ugh */
    rotate(315deg);
  width: 20px;
}

th { vertical-align: top; }
td { vertical-align: top; }

code a { color: #AAA; }

.menu li { display: inline; margin-right: 1em; }

@keyframes flickerAnimation {
  0%   { opacity:1; }
  50%  { opacity:0; }
  100% { opacity:1; }
}
@-webkit-keyframes flickerAnimation{
  0%   { opacity:1; }
  50%  { opacity:0; }
  100% { opacity:1; }
}
.animate-flicker {
   -webkit-animation: flickerAnimation 1s infinite;
    animation: flickerAnimation 1s infinite;
}

.bar {
  fill: steelblue;
}

.bar-number {
  padding-right: 2px;
  font-family: sans-serif;
  text-align: right;
  font-size: 0.7em;
  vertical-align: center;
}



